let brigeCallbackMap = {};
export const addBrigeCallback = (name,callback) => {
    let callbackList;
    if(!window[name]){
        callbackList = brigeCallbackMap[name] = [];
        window[name] = function() {
            const args = arguments;
            callbackList.forEach(callback => {
                callback.apply(null,args);
            });
        }
    }else{
        callbackList = brigeCallbackMap[name];
    }
    callbackList.push(callback);
};

export const removeBrigeCallback = (name, callback) =>{
    let callbackList = brigeCallbackMap[name];
    if(callbackList){
        if(!callback){
            callbackList.length = 0;
        }else{
            const index = callbackList.findIndex((item) => {
                return item === callback;
            });
            callbackList.splice(index,1);
        }
    }
};