import React, { Component } from 'react';
import jsCookie from "js-cookie";
import config from '../../utils/config';
import utils from '../../utils/utils';
import axios from 'axios';
import Timer from '../../utils/Timer';
import {addBrigeCallback,removeBrigeCallback} from "../../utils/brigeForNative";
import {
    CAN_NOT_RECEIVE_SMS_CODE,
    GO_TO_SMS_DESC,
    USE_VOICE_CODE_DESC,
    GET_VOICE_CODE,
    COMPELETE,
    OPERATION_OVER_RUN,
    OPERATION_TOO_FREQUENT,
} from '../../config/copywriting';
import { SEND_ONE_TIME_PASSWORD } from '../../config/interfaceUrl';
import BasicButton from '../../components/BasicButton';
import confirm from '../../components/Confirm/Confirm';

import styles from './index.module.scss';

let isNetRequesting = false;//网络请求防抖
let speechSessionId = '';
let account = ''; // 账户信息
const from = utils.getQueryParams('from');
const inMac = from === 'mac';

const timer = new Timer();
const {BRIGE_CALLBACK_NAME_FOR_GET_REMAINING_TIME} = config;
const GET_VERCODE_ERROR_TOO_OFTEN="otp.too.often.error";
const GET_VERCODE_ERROR_LIMIT_REACHED="otp.limit.reached.error";

export default class RegistrationSmsHelpComp extends Component {

    constructor(props) {
        super(props);
        this.state = {
            inMac:false,
            vercodeBtnUseable: true,
        }
    }

    postMessageToMac(action,params){
        try {
            inMac && window.webkit.messageHandlers[action].postMessage(params);
        } catch(err) {
            console.log(err);
        }
    }
    remainingTimeCallback(remainingTime){
        console.log(remainingTime);
        timer.startTimer({
            count:remainingTime,
            stepCallback(time){
                console.log(time);
            }
        })
    }

    componentDidMount() {
        account = jsCookie.get('registrationSpeechAccount');
        this.setState({
            inMac
        });
        addBrigeCallback(BRIGE_CALLBACK_NAME_FOR_GET_REMAINING_TIME,this.remainingTimeCallback);
        this.postMessageToMac('remainingTime',{callback:BRIGE_CALLBACK_NAME_FOR_GET_REMAINING_TIME});
    }

    /**
     * 关闭本页
     */
    closeCurPage() {
        window.opener = null;
        window.open('', '_self');
        window.close();
        this.postMessageToMac('closeWindow','');
        // window.wx.closeWindow && window.wx.closeWindow();
    }


    /**
     * 获取语音验证码
     */
    getSpeechVerCode(){
    
        //验证码必须有合法手机号
        if (!account) {
            return;
        }

         //防抖
         if (isNetRequesting) {
            return;
        }
        isNetRequesting = true;

        let recipient = account;
        let sig;
        let params;
        if(speechSessionId){
            params = {
                sessionId: speechSessionId,
                recipient,
                voiceotp: ''
            }
        }else{
            params = {
                recipient,
                voiceotp: ''
            }
        }
        sig = utils.getSignByParam(params);

        axios.get(SEND_ONE_TIME_PASSWORD, {
            params: {sig,...params}
        }).then((data) => {
            if (data && data.data && data && data.data.success) {
                if (data.data.sessionId) {
                    speechSessionId = data.data.sessionId;
                    jsCookie.set('speechSessionId',data.data.sessionId,{expires:1/24/6}); //把获取到的验证码存到 cookie 内,验证码十分钟过期
                    this.postMessageToMac('sendCode',{sessionId:speechSessionId,c:data.data.c || ''});
                    this.closeCurPage();
                }
            }else{
                if (data.data.errors && data.data.errors.length > 0) {
                    let error = data.data.errors[0];
                    switch (error.code) {
                        case GET_VERCODE_ERROR_TOO_OFTEN:
                            confirm({ content:OPERATION_TOO_FREQUENT });
                            break;
                        case GET_VERCODE_ERROR_LIMIT_REACHED:
                            confirm({ content:OPERATION_OVER_RUN });
                            break;
                        default:
                            break;
                    }
                }
            }
        }).catch(function (error) {
            console.log(error);
        }).finally(function () {
            isNetRequesting = false; 
        });
    }

    componentWillUnmount(){
        removeBrigeCallback(BRIGE_CALLBACK_NAME_FOR_GET_REMAINING_TIME,this.remainingTimeCallback);
    }

    goBack = () => {
        this.closeCurPage();
        // this.props.history.go(-1);
    }

    render() {
        return (
            <div className={styles.smsHelper}>
                <div className={styles.textWrapper}>
                    <div className={styles.title}>
                        {CAN_NOT_RECEIVE_SMS_CODE}
                    </div>
                    <ol className={styles.content}>
                        <li className={styles.block}>
                            <div>{GO_TO_SMS_DESC}</div>
                        </li>
                        <li>
                            <div>{USE_VOICE_CODE_DESC}</div>
                        </li>
                    </ol>
                </div>
                <BasicButton
                    text={GET_VOICE_CODE}
                    buttonWrapperClass={styles.SmsBtn}
                    onClick={this.getSpeechVerCode.bind(this)}
                />
                <BasicButton
                    text={COMPELETE}
                    buttonWrapperClass={styles.CompleteBtn}
                    onClick={this.goBack}
                />
            </div>
        )
    }
}
